@use "../abstracts/variables";
@use "../abstracts/mixins";

.wrapper__slider {
  position: relative;
  z-index: 0;

  .swiper-wrapper {
    position: relative;
  }

  button.swiper-button {
    position: absolute;
    top: 50%;
    z-index: 2;
    display: none;
    width: 52px;
    height: 52px;
    border-style: none;
    transform: translateY(-50%);
    border-radius: 50%;
    @include mixins.clickEffect($time: .3s, $effect: translateY(-50%) scale(.98));

    @include mixins.media('tablet-l') {
      @include mixins.boxFlex(center, initial);
    }

    &-prev {
      left: -100px;

      & > svg {
        transform: rotateZ(-180deg);
      }
    }

    &-next {
      right: -100px;
    }

    &.hidden-arrows {
      display: none;

      @include mixins.media('tablet-l') {
        display: block;
      }
    }

    &:disabled {
      opacity: .5;
      pointer-events: none;
      cursor: initial;
    }
  }

  &.defer {
    @media screen and (max-width: 768px) {
      & .slider_defer > .swiper-wrapper {
        overflow-x: scroll;
        display: flex;
        @include mixins.marginsInside();

        & > * {
          min-width: 252px;
          margin-right: 16px;
          flex-shrink: 1;

          &:first-of-type {
            margin-left: variables.$paddings-side--movil;
          }

          &:last-of-type {
            margin-right: variables.$paddings-side--movil;
          }
        }
      }
    }
  }
}


.swiper-pagination {
  @include mixins.boxFlex();
}

.swiper-pagination-bullet {
  display: block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: variables.$color-white;
  border: 1px solid variables.$color-principal;
  cursor: pointer;
  margin: 3px;

  &-active {
    background-color: variables.$color-principal;
  }
}

.vertical__upsell {
  .wrapper__slider {
    height: 100vh;
    .swiper-wrapper {
      height: 100vh;
    }

    button.swiper-button {
      position: absolute;
      z-index: 1;
      border-style: none;
      transform: translateY(-50px);

      &.swiper-vertical-button {
        &-prev {
          top: 50px;
          left: 43px;
        }

        &-next {
          top: initial;
          right: 48px;
          bottom: -8px;
        }
      }
    }
  }
}
