
@use '../abstracts/variables';
@use '../abstracts/mixins';

.reassurence {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: variables.$color-secondary;

  @include mixins.media('tablet') {
    padding-top: 44px;
    padding-bottom: 38px;
  }
}

.reassurence__wrapper {
  justify-items: center;
  @include mixins.boxGrid(1fr, repeat(4, 1fr), 0, 28px);

  @include mixins.media('tablet') {
    @include mixins.boxGrid(repeat(4, 1fr), 1fr, 24px, 0);
  }
}

.reassurence__card {
  max-width: 252px;
  color: variables.$color-principal;
  @include mixins.boxFlex(center, center, column);

  @include mixins.media('tablet') {
    @include mixins.boxFlex(initial, center, column);
  }
}

.reassurence__icon {
  display: block;
  height: 46px;
  margin: 0 auto 4px;
  width: 46px;

  @include mixins.media('tablet') {
    margin-bottom: 26px;

  }
}

.reassurence__text,
.reassurence__title {
  line-height: variables.$line-height--general-up-s;
  text-align: center;
}

.reassurence__title {
  margin-bottom: 6px;
  width: 90%;
  color: variables.$color-principal;
  font-weight: 400;
  text-transform: uppercase;
  font-size: variables.$font-size--general-up-s;
}

.reassurence__text {
  color: variables.$color-black;
  font-size: variables.$font-size--general-s;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;

  @include mixins.media('tablet') {
    margin-top: auto;
    max-width: 250px;
    height: 80px;
  }
}
