@use "../abstracts/variables";
@use "../abstracts/mixins";

nav .product-card .product-card__status-tag{
  @media (min-width: 1100px) and (max-width: 1400px) {
    font-size:0.7vw !important;
  }
}
.product-card {
  position: relative;
  height: 100%;
  overflow: hidden;
  border-radius: 0px;
  @include mixins.boxFlex(initial, initial, column);

  & > a {
    height: 100%;
  }

  &--head {
    display: flex;
    flex-direction: column;

    &-images {
      position: relative;
      overflow: hidden;
      padding-bottom: 100%;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.4s;
      }
    }

    .product-card__image-2 {
      opacity: 0;
    }

    &-images:hover .product-card__image-2 {
      @include mixins.media('tablet-l') {
        opacity: 1;
        z-index: 1;
      }
    }

    &-info {
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0 auto;
      color: variables.$color-text;
      font-size: variables.$font-size--general-m;
      font-weight: 300;
      text-align: center;
      @include mixins.boxFlex(center, center, column);
      justify-content: flex-start;

      span:last-child {
        display: block;
        width: 100%;
      }
    }

    &-info.wk-product-info {
      margin-top: 0;
    }

    .product-card__title {
      font-size: variables.$font-size--general-up-s;
      line-height: variables.$line-height--general-up-s;
    }
  }

  .wk-button.wk-button--remove.wk-button--floating,
  .wk-button.wk-button--add.wk-button--floating {
    padding: 0;
    top: 15px;
    right: 22px;

    .wk-icon.wk-button__icon {
      height: 18px;
      width: 18px;

      @include mixins.media("tablet") {
        height: 22px;
        width: 22px;
      }
    }

    .wk-icon__svg path {
      stroke-width: 2px;
    }
  }

  .product-card__status-tag-wrapper {
    position: absolute;
    top: 17.5px;
    left: 22px;
    z-index: 1;
    width: 68%;
    color: variables.$color-white;
    flex-wrap: wrap;
    @include mixins.boxFlex(initial);

    @include mixins.mediaCustom("min-width: 480px") {
      width: initial;
    }
  }



  .product-card__status-tag {
    margin-bottom: 5px;
    padding: 0 6px;
    font-size: variables.$font-size--general-xs;
    line-height: variables.$line-height--general-xs;

    @include mixins.media("tablet") {
      font-size: variables.$font-size--general-up-s;
      line-height: variables.$line-height--general-up-s;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__brand {
    font-family: variables.$font-family--title;
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-heigh--general-l;
    font-weight: 900;
    color: variables.$color-black;
    text-transform: uppercase;
  }

  &__condition {
    color: variables.$color-background-2;
    line-height: 18px;
  }

  &__title + span {
    display: block;
    margin-top: auto;
    font-size: variables.$font-size--general-up-s;
  }

  .product.price {
    @include mixins.boxFlex();

    .compare,
    .regular {
      overflow: hidden;
      width: fit-content;
      text-overflow: clip;
      white-space: nowrap;
      line-height: variables.$line-height--general-m;
      font-weight: 300;
    }

    .regular {
      font-size: variables.$font-size--general-m;
    }

    .compare {
      margin-right: 10px;
      font-size: variables.$font-size--general-up-m;
      color: variables.$color-background-2;
    }

    .compare + .regular {
      color: variables.$color-accent-3;
    }
  }

  &--form {
    display: flex;
    flex-direction: column;
    margin-top: auto;

    &-variants {
      width: 95%;
      margin: 0 auto 10px;
    }

    button {
      width: 95%;
      min-height: 42px;
      margin: 0 auto 10px;
      font-size: variables.$font-size--general-m;
    }
  }
}

/* This was moved in order to tag styles were taken in main product */
.product-card__status-tag-wrapper {
  position: absolute;
  top: 17.5px;
  left: 22px;
  z-index: 2;
  width: 68%;
  color: variables.$color-white;
  flex-wrap: wrap;
  @include mixins.boxFlex(initial);

  @include mixins.mediaCustom('min-width: 480px') {
    width: initial;
  }
}

.product-card__status-tag {
  margin-bottom: 5px;
  padding: 0 6px;
  font-size: variables.$font-size--general-xs;
  line-height: variables.$line-height--general-xs;

  @include mixins.media('tablet') {
    font-size: variables.$font-size--general-up-s;
    line-height: variables.$line-height--general-up-s;
  }

  &:not(:last-child) {
    margin-right: 10px;
  }
}

.header .product-card {
  .wk-icon.wk-button__icon {
    height: 20px;
    width: 20px;
  }

  .product-card--head-images {
    padding-bottom: 94%;
    img {
      object-fit: contain;
      padding: 12px;
      top: 12px;
    }
  }

  .product-card__title {
    line-height: 18px;
    @include mixins.excerptForLine(2);
  }

  .product-card__status-tag-wrapper{
    top:0px;
    left: 0px;
  }

  &:has(.wk-button) .wk-button {
    align-items: start;
    min-width: 0px;
    width: 22px;
  }
}
