
@use '../abstracts/variables';
@use '../theme/theme';
@use '../abstracts/mixins';

.footer-linklist {
  background-color: variables.$color-white;
  padding-top: 51px;
  padding-bottom: 51px;
  @include mixins.boxGrid(1fr, auto, 0, 22px);

  @include mixins.media('tablet-l') {
    @include mixins.boxGrid(6fr 1.5fr 1.5fr, 1.2fr, max(40px), 0);
  }

  @include mixins.media('desktop-s') {
    column-gap: max(60px);
  }

  @include mixins.media('desktop-m') {
    @include mixins.boxGrid(6fr 1.5fr 2fr, 1.2fr, max(80px), 0);
  }

  li {
    list-style: none;
  }

  .h3 {
    text-transform: uppercase;
    margin-bottom: 18px;
    color: variables.$color-black;
    @extend .h6;
  }
}

.footer-linklist__links {
  justify-items: center;
  text-align: center;
  @include mixins.boxGrid(1fr, auto, 0, 15px);

  @include mixins.mediaCustom('min-width: 340px') {
    text-align: initial;
    @include mixins.boxGrid(repeat(2, 1fr), auto, 0, 15px);
  }

  @include mixins.media('tablet-l') {
    justify-items: initial;
    @include mixins.boxGrid(repeat(4, 1fr), 1fr, 40px);
  }

  @include mixins.media('desktop') {
    column-gap: 25px;
  }

  @include mixins.media('desktop-m') {
    @include mixins.boxGrid(repeat(4, 1fr), 1fr, 57px);
  }

  .footer-list {
    @include mixins.mediaCustom('max-width: 991px') {
      min-width: 150px;
    }
  }

  a {
    line-height: 22.5px;
    font-size: variables.$font-size--general-up-s;
    font-weight: 400;
    text-transform: uppercase;
    color: variables.$color-black;
    @include mixins.linkEffect(currentColor, 1px, false);

    &:after {
      bottom: 5px; // To overwrite
    }
  }

  li:not(:last-child) a {
    margin-bottom: 12px;
  }
}

.footer-linklist__languages,
.footer-linklist__currency {
  ul {
    @include mixins.boxFlex(center, initial);

    @include mixins.media('tablet-l') {
      justify-content: flex-end;
    }
  }
}

.footer-linklist__currency {
  display: none;

  button {
    margin-left: auto;
    margin-right: auto;
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-heigh--general-l;
    @include mixins.boxFlex(initial, center);
    @include mixins.clickEffect;

    @include mixins.media('tablet-l') {
      margin-right: 0;
      margin-left: auto;
    }

    svg {
      height: 15px;
      margin-left: 9px;
      width: 15px;
    }
  }

  ul[data-active] {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    z-index: 1;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    width: 80px;
    background-color: variables.$color-white;
    transition: all .4s;
    overflow: hidden;

    @include mixins.media('tablet-l') {
      margin-right: 0;
      margin-left: auto;
      right: 0;
      transform: translateX(0);
    }
  }

  .shopify-localization-form {
    position: relative;
  }

  ul[data-active="false"] {
    height: 0;
    padding: 0 5px;
  }

  ul[data-active="true"] {
    margin-top: 10px;
    padding: 5px;
    box-shadow: 0px 0px 12px -10px variables.$color-principal;
  }

  a {
    color: variables.$color-black;
  }

  li {
    margin-bottom: 10px;
  }
}

.footer-linklist__languages {
  img {
    height: 16px;
    width: 24px;
  }

  li {
    line-height: 0;
    margin-bottom: 6px;
    border: 3px solid transparent;
    @include mixins.clickEffect;

    &:not(:first-of-type) {
      margin-left: 12px;
    }

    a {
      display: block;
    }
  }

  li.isCurrent {
    padding: 1px;
    border-color: variables.$color-accent-3;
  }
}

.footer-linklist__icons-component {
  text-align: center;
  @include mixins.boxGrid(1fr, 1fr, 0, 20px);

  @include mixins.media('tablet-l') {
    grid-template-rows: auto;
    row-gap: 12px;
    text-align: right;
  }

  &.left-component {
    @include mixins.boxFlex(initial, initial, column);
  }
}

.footer-linklist__social-media {
  img {
    height: 25px;
    width: 25px;

    &:not(:first-of-type) {
      margin-left: 7px;
    }
  }
}

.footer-linklist__payment {
  img {
    height: 16px;
    width: 24px;
    margin-bottom: 6px;

    &:not(:first-of-type) {
      margin-left: 12px;
    }
  }
}

.footer-linklist__trustpilot,
.footer-linklist__apps-store {
  box-shadow: unset;

  img {
    height: auto;
    box-shadow: unset;
  }
}

.footer-linklist__apps-store {
  svg {
    &:not(:first-of-type) {
      margin-left: 9px;
    }
  }
}
