@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "./mes-alerts";

// General styles
.mes-alerts-account__header-table {
  display: none;

  @include mixins.media('tablet-l') {
    margin-bottom: 5.5px;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
    @include mixins.boxGrid(85px repeat(6, 1fr), auto, 10px, 0);
  }
}

item-alert {
  align-items: center;
  padding: 16px;
  border: 1px solid variables.$color-principal;
  @include mixins.boxGrid(auto, auto, 0, 8px);

  @include mixins.media('tablet-l') {
    justify-content: space-between;
    padding: 20px 0;
    width: 100%;
    border: unset;
    border-bottom: 1px solid variables.$color-background-2;
    @include mixins.boxGrid(85px 1fr 14.4%, auto, 10px, 0);
  }

  &:not(:last-of-type) {
    margin-bottom: 1.5rem;

    @include mixins.media('tablet-l') {
      margin-bottom: 0;
    }
  }

  &:first-of-type {
    @include mixins.media('tablet-l') {
      border-top: 1px solid variables.$color-background-2;
    }
  }
}

.mes-alerts-account__table-code-alert {
  padding: 8px;
  width: 100%;
  background-color: variables.$color-secondary;
  text-align: center;

  @include mixins.media('tablet-l') {
    padding: 0;
    width: auto;
    background-color: unset;
    text-align: left;
  }

  span {
    font-weight: 600;
    color: variables.$color-principal;
  }
}

.mes-alerts-account__edit {
  button {
    font-weight: 300;
    width: 100%;
    white-space: wrap;

    @include mixins.media('tablet-l') {
      padding: 5px 16px;
      font-size: variables.$font-size--general-xs;
    }
  }
}

.mes-alerts-account__qty {
  margin-right: 16px;
  color: variables.$color-error;
  font-size: variables.$font-size--general-s;
  line-height: variables.$line-height--general-s;
}

.mes-alerts-account__table-item {
  @include mixins.boxGrid(auto, repeat(5, 1fr), 10px, 0);

  @include mixins.media('tablet-l') {
    @include mixins.boxGrid(repeat(5, 1fr), auto, 10px, 0);
  }
}

.mes-alerts-account__table-subitem {
  text-transform: capitalize;
  font-size: variables.$font-size--general-m;
  line-height: variables.$line-height--general-m;
  @include mixins.boxFlex(space-between);

  &:not(:last-of-type) {
    margin-bottom: 8px;

    @include mixins.media('tablet-l') {
      margin-bottom: 0;
    }
  }

  & > span:first-of-type {
    @include mixins.media('tablet-l') {
      display: none;
    }
  }
}

.mes-alerts-account__form-header {
  display: none;
}

.mes-alerts-account__container {
  & > .loading {
    display: block;
    margin: 15% auto 0;
  }

  .mes-alerts-modal {
    @extend .mes-alerts-modal;

    .description-modal__description {
      padding: 0;
      margin-bottom: 1.5rem;
      background-color: unset;
    }

    .models-select {
      opacity: .5;
      pointer-events: none;
      transition: opacity .1s ease-out;
    }

    .models-select.models-enable {
      opacity: initial;
      pointer-events: initial;
    }
  }
}

#overlay--js-mes-alerts-delete-confirm {
  pointer-events: none;
}


// Pass to edit step

.mes-alerts-account__header-controls {
  margin-bottom: 1.5rem;
  @include mixins.boxFlex(space-between);

  .mes-alerts-modal,
  .mes-alerts {
    padding: 0;
  }

  .mes-alerts {
    margin-left: auto;
  }

  .mes-alerts__response--edit {
    margin-top: 20%;
  }
}

.mes-alerts-account__header-controls.isEditForm {
  display: block;
  margin-bottom: 0;

  .mes-alerts__open-btn,
  .mes-alerts-account__qty,
  .description-modal.mes-alerts-modal .description-modal__description,
  .mes-alerts,
  .description-modal__title {
    display: none;
  }

  .mes-alerts-account__form-header {
    @include mixins.boxFlex(space-between);
  }

  .mes-alerts-account__current-alert {
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
    font-weight: 600;
    color: variables.$color-principal;
  }

  .models-select-edit {
    opacity: .5;
    pointer-events: none;
    transition: opacity .1s ease-out;
  }

  .models-select-edit.models-enable {
    opacity: initial;
    pointer-events: initial;
  }

  .mes-alerts-account__btn-delete {
    margin-top: 1.5rem;
    width: 100%;
  }

  .description-modal.mes-alerts-modal {
    display: block;
    transform: unset;
    position: initial;
    margin: 0 auto;

    .description-modal__close {
      display: none;
    }
  }
}

// Modal confirm delete
.description-modal.mes-alerts__delete-confirm {
  height: auto;

  @include mixins.mediaCustom('min-width: 620px') {
    max-width: 400px;
  }

  @include mixins.mediaCustom('min-width: 750px') {
    padding: 30px;
  }

  .description-modal__close,
  .description-modal__description {
    display: none;
  }

  .mes-alerts__message-delete {
    @include mixins.boxFlex(center, center, column);

    & > span {
      text-align: center;
      font-size: variables.$font-size--general-l;
      line-height: variables.$line-height--general-l;
      color: variables.$color-black;
    }

    & > div:first-of-type {
      margin-top: 1.5rem;
      width: 100%;
      @include mixins.boxFlex(space-evenly);
    }
  }

  .mes-alerts__response.form__message {
    border: unset;
  }
}
