@use "../abstracts/variables";
@use "../abstracts/mixins";

.reviews {
  position: relative;
  padding: 45px 0;
  background-color: variables.$color-secondary;

  @include mixins.media("tablet-l") {
    padding: 38px 0;
  }

  &__title {
    text-align: center;
  }

  &__single {
    display: flex;
    flex-direction: column;
    height: 100%;

    &-description,
    &-author {
      text-align: center;
      font-family: variables.$font-family--general;
      color: variables.$color-black;
    }
    &-description {
      font-size: 14px;
      line-height: 1.7em;
    }
    &-author {
      font-size: 15px;
      line-height: 24px;
      font-weight: 600;
      margin-top: auto;
      padding-top: 10px;
    }
  }

  &__slider {
    padding: 0 32px;
    width: 100%;
    margin: 0 auto;

    @include mixins.media("tablet-l") {
      width: 80%;
      max-width: variables.$max-width--wrapper;
    }

    .swiper-wrapper {
      margin-bottom: 110px;
      align-items: stretch;

      & > .swiper-slide {
        height: auto;
      }

      @include mixins.media("tablet-l") {
        margin-bottom: 101px;
      }
    }

    &.unique-slide {
      .swiper-wrapper {
        justify-content: center;
      }

      .swiper-slide {
        margin-right: 0 !important;
      }
    }

    &--no-icon {
      .swiper-wrapper {
        margin-bottom: 0;
      }
    }
  }

  &__icon {
    position: absolute;
    bottom: 72px;
    left: 50%;
    transform: translateX(-50%);
    @include mixins.boxFlex();

    @include mixins.media("tablet-l") {
      bottom: 75px;
    }

    img {
      width: 130px;
      height: 61px;

      @include mixins.media("tablet-l") {
        width: 107px;
        height: 50px;
      }
    }
  }

  &__slider.notPagination + .reviews__icon  {
    @include mixins.media("tablet-l") {
      bottom: 38px;
    }
  }
}
