@use '../abstracts/variables';
@use '../abstracts/mixins';

.error-page {
  padding: 112px 24px 165px;

  @include mixins.media('tablet') {
    padding: 174px 24px 174px;
  }

  &__title, &__text {
    text-align: center;
  }

  &__title {
    margin: 0 0 variables.$margin-xs;
    font-size: 48px;
    line-height: 1em;

    @include mixins.mediaCustom('min-width: 350px') {
      margin: 0;
      font-size: 100px;
    }
    @include mixins.media('tablet') {
      font-size: 120px;
    }
  }

  &__text {
    margin-bottom: 44px;
    font-weight: variables.$font-regular;

    @include mixins.media('tablet') {
      margin-bottom: 47px;
    }
  }

  &__btn {
    min-width: 185px;
    margin: 0 auto;
    font-size: variables.$font-size--general-up-s;
  }
}
