@use "../abstracts/variables";
@use "../abstracts/mixins";

.form__wrapper {
  .form__textfield {
    @include mixins.boxFlex(center, center, column);
    text-align: center;
  }
}

.form__label-textfield {
  label {
    display: block;
    margin-bottom: 4px;
    text-align: center;
    position: absolute;
    font-size: 16px;
    top: 22px;
    transform: translateY(-50%);
    transition: 0.5s ease;
  }

  input,
  textarea,
  select {
    border: 1px solid variables.$color-black;
    margin-bottom: 8px;
  }

  input[type="search"] {
    width: 100%;
    outline: none;
  }

  input,
  textarea {
    padding: 8px 5px;
    text-align: center;
    &:focus + label,
    &:not(:placeholder-shown) + label {
      font-size: 12px;
      top: 0;
      transform: translateY(5px);
      opacity: 0.7;
    }

    &::-webkit-search-cancel-button {
      appearance: none;
      -webkit-appearance: none;
    }
  }

  select {
    padding: 10px 5px;
    text-align-last: center;
    background: none;
    appearance: none;
    background: url(data:image/svg+xml;base64,)
      no-repeat 100%;
  }

  &--spaced {
    margin-bottom: 0.5rem;
  }
}

/* form inputs animation */
.form__textfield .input-container,
.form__textfield-notlabel .input-container {
  position: relative;
  @include mixins.boxFlex(initial, center);

  label,
  span {
    position: absolute;
    top: 24px;
    padding: 0 10px;
    text-transform: uppercase;
    font-size: variables.$font-size--general-up-s;
    transform: translateY(-50%);
    transition: 0.5s ease;
  }

  input,
  textarea {
    width: 100%;
    outline: none;

    &::placeholder {
      // color: transparent;
    }

    &:focus ~ label,
    &:focus ~ span,
    &:not(:placeholder-shown) ~ label,
    &:not(:placeholder-shown) ~ span {
      top: 0;
      font-size: 10px;
      transform: translateY(5px);
      opacity: 0.5;
    }
  }

  svg {
    position: absolute;
    right: 14px;
  }
}

.form__textfield,
.form__textfield-notlabel {
  label {
    display: none;
  }
}

.form__textfield-label {
  label {
    display: block;
    text-align: center;
    margin-bottom: 4px;
  }
}

.form__textfield-label,
.form__textfield,
.form__textfield-notlabel {
  input,
  textarea,
  select {
    border: 1px solid variables.$color-background;
    transition: all 0.3s;

    &:hover {
      border-color: variables.$color-principal;
    }

    &:focus {
      border-color: variables.$color-background-3;
    }
  }

  input {
    font-size: variables.$font-size--general-up-s;
    line-height: variables.$line-height--general-up-s;
  }

  input::placeholder {
    text-transform: uppercase;
  }

  input,
  textarea {
    width: 100%;
    padding: 7px 12px;
  }

  select {
    width: 100%;
    padding: 15.5px 10px;
    outline: none;
    background: none;
    appearance: none;
    background: var(--icon-caret) no-repeat calc(100% - 16px);
    cursor: pointer;
  }

  &.size-s select {
    padding: 10.5px 10px;
  }

  &.size-m select {
    padding: 15.5px 10px;
  }

  &.size-s .input-container {
    input,
    textarea {
      padding: 11px 10px;
    }

    label {
      top: 20px;
    }
  }

  &.size-m .input-container {
    input,
    textarea {
      padding: 15.5px 12px;
    }

    label {
      top: 25px;
    }
  }

  &--spaced {
    margin-bottom: 0.5rem;
  }
}

.form__textfield {
  @include mixins.boxFlex(initial, initial, column);
  & > label {
    font-size: variables.$font-size--general-s;
    text-transform: initial;
  }
}

.form__checkbox {
  font-size: variables.$font-size--general-up-s;
  @include mixins.boxFlex(initial);

  input {
    position: absolute;
    opacity: 0;
    width: 16px;
    height: 16px;
    @include mixins.clickEffect;

    &:checked ~ .slider {
      background-image: var(--icon-check);
      background-repeat: no-repeat;
      background-color: variables.$color-principal--variant-3;
      background-size: 100%;
      background-position: 50% 40%;
    }
  }

  .slider {
    height: 16px;
    width: 16px;
    border: 1px solid variables.$color-background-4;
    margin-right: 8px;
    transition: all 0.3s;
    @include mixins.clickEffect;
  }

  input:disabled ~ .slider,
  input.disabled ~ .slider {
    background-color: variables.$color-background-5;
    border-color: variables.$color-background-4;
    cursor: initial;
    transform: unset;
    pointer-events: none;
  }

  &:hover input:not(:checked, input:disabled, input.disabled) ~ .slider {
    border-color: variables.$color-principal--variant-2;
  }

  &:hover input:not(input:disabled, input.disabled):checked ~ .slider {
    border-color: variables.$color-principal--variant-1;
    background-color: variables.$color-principal--variant-1;
  }

  &:active input:not(:checked, input:disabled, input.disabled) ~ .slider {
    border-color: variables.$color-principal--variant-3;
  }

  &:active input:not(input:disabled, input.disabled):checked ~ .slider {
    border-color: variables.$color-principal--variant-2;
    background-color: variables.$color-principal--variant-2;
  }

  &:focus input:not(:checked, input:disabled, input.disabled) ~ .slider {
    border-color: variables.$color-background-6;
    border-width: 2px;
  }

  &:focus input:not(input:disabled, input.disabled):checked ~ .slider {
    border-color: variables.$color-background-6;
    border-width: 2px;
    background-color: variables.$color-principal--variant-1;
  }
}

.toggle__checkbox {
  align-items: center;

  .slider {
    height: 24px;
    width: 48px;
    border-radius: 12px;
    border: 1px solid rgb(179, 179, 179);
    margin-right: 10px;
    display: block;
    position: relative;
    cursor: pointer;
    background: variables.$color-background;
    -webkit-transition: 0.2s;
    transition: 0.2s;

    &:after {
      height: 20px;
      width: 20px;
      top: 50%;
      left: 2px;
      border-radius: 50%;
      transform: translateY(-50%);
      position: absolute;
      content: "";
      background-color: variables.$color-white;
      transition: 0.5s ease;
    }
  }

  input {
    display: none;

    &:checked + .slider {
      background: var(--color-base-principal);

      &:after {
        left: auto;
        right: 2px;
        background-color: rgb(82, 82, 82);
      }
    }
  }
}

.form__radio-button {
  display: flex;
  align-items: center;

  input {
    accent-color: variables.$color-black;
    height: 20px;
    width: 20px;
    margin-right: 10px;
    display: none;

    &:checked ~ .checkmark {
      border-color: variables.$color-principal--variant-3;
      border-width: 4px;
    }
  }

  .checkmark {
    border-color: variables.$color-background-4;
    border-radius: 100%;
    border-style: solid;
    border-width: 1px;
    height: 16px;
    width: 16px;
    margin-right: 8px;
    transition: all 0.3s;
    @include mixins.clickEffect;
  }

  input:disabled ~ .checkmark,
  input.disabled ~ .checkmark {
    background-color: variables.$color-background-5;
    border-color: variables.$color-background-4;
    cursor: initial;
    transform: unset;
    pointer-events: none;
  }

  &:hover input:not(:checked, input:disabled, input.disabled) ~ .checkmark {
    border-color: variables.$color-principal--variant-2;
  }

  &:hover input:not(input:disabled, input.disabled):checked ~ .checkmark {
    border-color: variables.$color-principal--variant-1;
  }

  &:active input:not(:checked, input:disabled, input.disabled) ~ .checkmark {
    border-color: variables.$color-principal--variant-1;
  }

  &:active input:not(input:disabled, input.disabled):checked ~ .checkmark {
    border-color: variables.$color-principal--variant-2;
  }

  &:focus input:not(:checked, input:disabled, input.disabled) ~ .checkmark {
    border-color: variables.$color-principal--variant-1;
    border-width: 2px;
  }

  &:focus input:not(input:disabled, input.disabled):checked ~ .checkmark {
    border-color: variables.$color-principal--variant-2;
    border-width: 2px;
  }
}

.form__question {
  display: grid;
  grid-column-gap: 20px;
  column-gap: 20px;
  grid-row-gap: 20px;
  row-gap: 20px;
  text-align: left;

  &--answer {
    font-weight: 600;
  }
}

.form__message {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: variables.$font-size--general-up-s;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid;
  background-color: transparent;
  outline: none;

  &--success {
    color: variables.$color-success;
    border-color: variables.$color-success;
  }

  &--error {
    color: variables.$color-error;
    border-color: variables.$color-error;
    outline: none;
  }

  & > svg {
    width: 25px;
    height: 25px;
    margin-right: 10px;
  }
}

.icon-error {
  width: 18px !important;
  height: 18px !important;
  margin-right: 10px;
}

.list-errors {
  list-style: none;
}

.errors {
  & > ul {
    list-style: none;
  }

  a,
  a:visited {
    color: variables.$color-black;
  }

  margin-bottom: 20px;
  font-size: variables.$font-size--general-up-s;
}
