@use "../abstracts/variables";
@use "../abstracts/mixins";

.collage-custom {
  overflow: hidden;

  &:nth-child(2) {
    margin-top: variables.$margin-section;
  }

  &:hover {
    text-decoration: none;
  }

  &.section-space.wrapper-without-limit {
    @include mixins.mediaCustom("(max-width: 991px)") {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__title {
    text-align: center;

    @include mixins.media("tablet-l") {
      font-size: variables.$font-size--mobile--h1-customer;
      line-height: variables.$line-height--mobile--h1-customer;

      font-size: variables.$font-size--mobile-h1;
      line-height: variables.$line-height--mobile-h1;
    }
  }

  &__container {
    display: grid;
    gap: variables.$gap-s;
    grid-template-columns: repeat(1, minmax(375px, 1fr));
  }

  &__card {
    position: relative;
    aspect-ratio: 1 / 1;
    overflow: hidden;
  }

  &__card--zoom {
    &:hover {
      & > img {
        transform: scale(1.1);
      }
    }
  }

  &__grid--3,
  &__grid--5,
  &__grid--7,
  &__grid--9 {
    .collage-custom__card:first-child {
      grid-column: 1 / 3;
    }
  }
  &__grid--3,
  &__grid--5 {
    grid-template-columns: 49.25% auto;
  }
  &__grid--3 {
    grid-template-rows: auto auto;
  }
  &__grid--4,
  &__grid--6,
  &__grid--8,
  &__grid--9 {
    grid-template-columns: auto auto;
  }
  &__grid--4 {
    grid-template-columns: 50% 50%;
    grid-template-rows: 49.25% 49.25%;
  }
  &__grid--5,
  &__grid--6 {
    grid-template-rows: auto auto auto;
  }
  &__grid--7 {
    grid-template-rows: 40% 40% auto;
    grid-template-columns: 49.4% auto;

    .collage-custom__nested {
      display: grid;
      gap: variables.$gap-s;
      grid-column: 1 / 3;
      grid-template-columns: 49.25% 49.25%;
      grid-template-rows: auto auto;

      .collage-custom__card {
        grid-column: auto;
      }
    }
  }
  &__grid--8 {
    grid-template-rows: auto auto auto auto;
  }
  &__grid--9 {
    grid-template-rows: auto auto auto auto auto;
  }

  &__subtitle {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
    transform: translateY(-65%);

    &:-webkit-any(.collage-custom__subtitle--background) {
      transform: translateY(-50%);
    }

    &-content {
      position: relative;
      display: block;
      width: fit-content;
      max-width: 80%;
      padding: 10px 10px 0;
      margin: 0 auto;
      font-size: variables.$font-size--general-up-l;
      line-height: 1em;
      color: variables.$color-white;
      @include mixins.excerptForLine(2);

      @include mixins.media("tablet-l") {
        padding: 20px 46.5px 0;
      }
    }

    .overflow-hide {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 10px;
      width: 100%;

      @include mixins.media("tablet-l") {
        height: 20px;
      }
    }

    &--background {
      .collage-custom__subtitle-content,
      .overflow-hide {
        background-color: variables.$color-white;
      }
      .collage-custom__subtitle-content {
        padding: 10px;

        @include mixins.media("tablet-l") {
          padding: 20px 46.5px;
        }
      }
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    transition: 1s ease-out;
  }

  @include mixins.media("tablet") {
    &__container {
      height: 298px;
      gap: variables.$gap-s;

      &:is(.collage-custom__grid--4) {
        height: 222px;
      }
      &:is(.collage-custom__grid--5) {
        height: 482px;
      }
      &:is(.collage-custom__grid--6) {
        height: 623px;
      }
      &:is(.collage-custom__grid--7) {
        height: 936px;
      }
      &:is(.collage-custom__grid--8) {
        height: 472px;
      }
      &:is(.collage-custom__grid--9) {
        height: 700px;
      }
      &:-webkit-any(.collage-custom__grid--4) {
        height: 222px;
      }
      &:-webkit-any(.collage-custom__grid--5) {
        height: 482px;
      }
      &:-webkit-any(.collage-custom__grid--6) {
        height: 623px;
      }
      &:-webkit-any(.collage-custom__grid--7) {
        height: 936px;
      }
      &:-webkit-any(.collage-custom__grid--8) {
        height: 472px;
      }
      &:-webkit-any(.collage-custom__grid--9) {
        height: 700px;
      }
    }

    &__card {
      aspect-ratio: auto;
    }

    &__grid--2 {
      grid-template-columns: 49.25% auto;
      .collage-custom__card {
        height: 298px;
      }
    }
    &__grid--3 {
      grid-template-columns: 32.2% 32.2% auto;

      .collage-custom__card {
        height: 298px;
      }
      .collage-custom__card:first-child {
        grid-column: 1;
      }
    }
    &__grid--4 {
      grid-template-columns: 24% 24% 24% auto;

      .collage-custom__card {
        height: 222px;
      }
    }
    &__grid--5 {
      grid-template-columns: 48.3% 24.3% auto;

      .collage-custom__card:first-child {
        grid-column: 1;
        grid-row: 1 / 3;
        height: 482px;
      }
      .collage-custom__card:nth-child(n + 2) {
        height: 236px;
      }
      .collage-custom__card:nth-child(2) {
        grid-column: 2;
        grid-row: 1;
      }
      .collage-custom__card:nth-child(3) {
        grid-column: 3;
        grid-row: 1;
      }
      .collage-custom__card:nth-child(4) {
        grid-column: 2;
        grid-row: 2;
      }
      .collage-custom__card:last-child {
        grid-column: 3;
        grid-row: 2;
      }
    }
    &__grid--6 {
      grid-template-columns: 32.2% 32.2% auto;
      grid-template-rows: auto auto;

      .collage-custom__card {
        height: 307px;
      }
    }
    &__grid--7 {
      grid-template-columns: 49.25% auto;
      grid-template-rows: auto auto;

      .collage-custom__card:first-child,
      .collage-custom__nested {
        grid-column: auto;
      }
      .collage-custom__card,
      .collage-custom__nested {
        height: 458px;
      }
      .collage-custom__nested {
        display: grid;
        grid-template-columns: 49.25% auto;
        grid-template-rows: auto auto;

        .collage-custom__card {
          height: 222px;
        }
      }
    }
    &__grid--8 {
      grid-template-columns: 24% 24% 24% 24%;
      grid-template-rows: auto auto;

      .collage-custom__card {
        height: 227px;
      }
    }
    &__grid--9 {
      grid-template-columns: 23.8% 23.8% 23.8% 23.8%;
      grid-template-rows: auto auto auto;

      .collage-custom__card {
        height: 227px;
      }
      .collage-custom__card:first-child {
        height: 463px;
        grid-column: 1 / 3;
        grid-row: 1 / 3;
      }
    }
  }

  @include mixins.media("desktop-m") {
    &__grid--3,
    &__grid--6 {
      gap: variables.$gap-l;
    }
    &__grid--4,
    &__grid--5,
    &__grid--7,
    &__grid--9 {
      gap: variables.$gap-l;
    }
    &__grid--5 {
      .collage-custom__card:nth-child(n + 2) {
        height: 228px;
      }
    }
    &__grid--6 {
      .collage-custom__card {
        height: 298px;
      }
    }
    &__grid--7 {
      grid-template-columns: 49.25% auto;
      .collage-custom__nested {
        gap: variables.$gap-m;
      }
    }
    &__grid--8 {
      gap: variables.$gap-m;
      grid-template-columns: 24% 24% 24% auto;
    }
    &__grid--9 {
      .collage-custom__card {
        height: 222px;
      }
    }
  }
}
