@use "../abstracts/variables";

.contact {
  text-align: center;
  display: grid;
  grid-column-gap: 8px;
  column-gap: 8px;
  grid-row-gap: 8px;
  row-gap: 8px;
  margin: 0 auto;
  width: 100%;

  select, textarea {
    text-transform: uppercase;
    color: #8D8D8D;
  }

  textarea {
    height: 104px;
  }

  .send-contact {
    margin-top: 16px;
  }

  &.align-left {
    flex-direction: row;

    .contact-heading {
      width: 50%;
    }
  }

  &--description {
    padding: 10px;
    margin-bottom: 20px;

    strong {
      font-weight: 600;
    }
  }

  .error-check-js, .error-user-exist {
    display: none;
  }
}