@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "../theme/theme";

.sellers-buyers {
  &.section-space {
    @include mixins.mediaCustom("max-width: 991px") {
      padding: 0;
    }
  }

  h1 {
    text-align: center;
    margin-bottom: 16px;
  }
}

.sellers-buyers__card {
  position: relative;
  padding: 18px 18px 43px;
  background: variables.$color-secondary;

  .text-buttons > span {
    font-weight: 400;
    color: variables.$color-accent-3;
    @extend .h2;
    margin-bottom: 4px;

    @include mixins.media("tablet-l") {
      margin-bottom: 8px;
    }
  }

  h2.text-buttons__p {
    margin-bottom: 18px;
    color: variables.$color-black;
    font-family: variables.$font-family--general;
    font-size: variables.$font-size--general-m;
    font-weight: 300;
    line-height: variables.$line-height--general-m;
    max-width: 80%;

    @include mixins.media("tablet-l") {
      margin-bottom: 16px;
    }
  }

  .btn-link {
    margin: 0 auto;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -23px;
  }
}

.sellers-buyers__cards-container {
  margin-bottom: 22px;
  @include mixins.boxGrid(unset, repeat(2, 1fr), unset, 39px);

  @include mixins.media("tablet-l") {
    @include mixins.boxGrid(repeat(2, 1fr), unset, 20px, unset);
  }
}

.sellers-buyers__icons-container {
  flex-wrap: wrap;
  @include mixins.boxFlex(space-around);

  @include mixins.mediaCustom("min-width: 400px") {
    flex-wrap: initial;
    @include mixins.boxFlex(space-between);
  }
}

.sellers-buyers__icon {
  width: 45%;
  font-weight: 300;
  @include mixins.boxFlex;

  @include mixins.mediaCustom("min-width: 360px") {
    width: 30%;
  }

  @include mixins.mediaCustom("min-width: 400px") {
    width: auto;
  }

  &:last-of-type {
    margin-top: 8px;

    @include mixins.mediaCustom("min-width: 360px") {
      margin-top: 0;
    }
  }

  @include mixins.mediaCustom("min-width: 400px") {
    &:not(:last-of-type) {
      margin-right: 20px;
    }
  }

  img {
    height: 35px;
    width: 35px;
  }

  span {
    width: 90px;
    margin-left: 6px;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    text-align: left;
    color: variables.$color-black;

    @include mixins.mediaCustom("min-width: 400px") {
      width: auto;
      font-size: variables.$font-size--general-up-s;
      line-height: variables.$line-height--general-up-s;
    }
  }
}
