@use "../abstracts/variables";
@use "../abstracts/mixins";
@import "./sellers-buyers";

.services-section.wrapper-without-limit {
  @include mixins.mediaCustom("(max-width: 991px)") {
    padding-left: 0;
    padding-right: 0;
  }
}

.services {
  &__title {
    text-align: center;
    margin-bottom: 11px;

    @include mixins.media("tablet-l") {
      margin-bottom: 16px;
    }
  }

  .service {
    &__slide {
      height: 100%;
      @include mixins.boxFlex(center, center, column);

      @include mixins.media("tablet-l") {
        @include mixins.boxFlex(center, stretch);
      }
    }

    &__content {
      padding: 40px 20px 60px;

      @include mixins.mediaCustom("min-width: 375px") {
        padding: 40px 30px 60px;
      }

      // @include mixins.media("tablet") {
      //   width: 50%;
      //   padding: 45px;
      // }

      @include mixins.media("tablet-l") {
        width: 50%;
        padding: 45px;
        @include mixins.boxFlex(center, initial, column);
      }

      @include mixins.media("desktop-s") {
        padding: 45px 50px 45px 50px;
      }

      @include mixins.media("desktop-m") {
        padding: 45px 85px 45px 75px;
      }

      @include mixins.media("desktop-l") {
        padding: 45px 100px 45px 89px;
      }
    }

    &__image {
      width: 100%;
      height: 248px;
      object-fit: cover;

      @include mixins.media("tablet") {
        height: 450px;
      }

      @include mixins.media("tablet-l") {
        align-self: center;
        width: 50%;
        height: 100%;
      }
    }

    &__subtitle {
      font-weight: variables.$font-regular;
      text-align: center;
      margin-bottom: 14px;
      line-height: 31px;

      @include mixins.media("tablet-l") {
        margin-bottom: 16px;
        text-align: start;
        line-height: 37px;
      }
    }

    &__description,
    &__description p,
    &__description span {
      font-weight: variables.$font-light;
      font-size: variables.$font-size--general-m;
      line-height: variables.$line-height--general-m;
      text-align: center;
      color: variables.$color-black;

      @include mixins.media("tablet-l") {
        text-align: start;
      }
    }

    &__description {
      margin-bottom: 20px;

      @include mixins.excerptForLine(12);

      @include mixins.media("tablet-l") {
        padding-right: 40px;
      }

      @include mixins.media("desktop-s") {
        padding-right: 60px;
      }

      @include mixins.media("desktop-m") {
        padding-right: 70px;
      }
    }

    &__link {
      display: flex;
      width: fit-content;
      margin: 0 auto;

      @include mixins.media("tablet-l") {
        margin: 0;
      }
    }
  }

  .sellers-buyers__icons-container {
    margin: 20px 0;
    @extend .sellers-buyers__icon;
  }

  .sellers-buyers__icons-container.qty-4 {
    width: initial;
    @include mixins.boxGrid(repeat(2, 1fr), initial, 10px, 10px);

    @include mixins.media("desktop") {
      @include mixins.boxGrid(repeat(4, 1fr), initial, 15px);
    }

    .sellers-buyers__icon {
      width: initial;
      margin-right: 0;
    }
  }

  &.type-2 .service {
    &__slide {
      @include mixins.media("tablet-l") {
        flex-direction: row-reverse;
      }
    }
  }

  .slider__services {
    background-color: variables.$color-secondary;

    button.swiper-button {
      display: none;
    }

    button.swiper-button-next {
      right: 25px;
    }

    button.swiper-button-prev {
      left: 25px;
    }

    .swiper-pagination {
      padding: 0 0 25px;
      @include mixins.boxFlex();

      @include mixins.media("tablet-l") {
        bottom: 0;
        position: absolute;
        right: 50%;
        z-index: 1;
        transform: translateX(50%);
      }
    }

    .swiper-slide {
      height: 100% !important;
    }
  }
}
