@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "./wrapper";

.accordions {
  display: grid;
  grid-row-gap: 20px;
  row-gap: 20px;
  margin-bottom: 20px;

  @include mixins.media('tablet') {
    margin-bottom: variables.$margin-m;
  }
}

.accordion {
  &__section {
    margin-bottom: variables.$margin-section--movil;

    @include mixins.media('tablet-l') {
      margin-bottom: variables.$margin-section;
    }

    & > .btn-1 {
      margin: 0 auto;
    }

    .btn-link {
      font-size: variables.$font-size--general-up-s;
    }
  }

  &__title.h2,
  &__title.h4 {
    font-weight: 400;
    margin-bottom: variables.$margin-l;
    text-align: center;
  }
}

.accordion-item {
  &__content, &__button {
    background-color: variables.$color-secondary;
    color: variables.$color-principal;
  }

  &__content {
    overflow: hidden;
    max-height: 0;
    padding: 0 16px;
    color: variables.$color-black;

    p:empty {
      margin-bottom: variables.$margin-xs;

      &:first-of-type {
        display: none;
      }
    }

    ul {
      padding-left: 16px;
    }

    ol {
      counter-reset: item;
    }

    ol li {
      display: block;
    }

    ol li::before {
      content: counter(item) ". ";
      counter-increment: item;
      width: 1.25em;
      display: inline-block;
    }

    a {
      color: variables.$color-principal;
      @include mixins.linkEffect(currentColor, 1px);
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 16px;
    cursor: pointer;
    @include mixins.clickEffect;

    span {
      max-width: calc(100% - 40px);
      font-size: variables.$font-size--general-up-m;
      line-height: variables.$line-height--general-m;
      color: variables.$color-principal;

      @include mixins.media('tablet') {
        font-size: variables.$font-size--general-up-l;
        line-height: variables.$line-heigh--general-l;
      }
    }

    svg {
      height: 16px;
      margin-left: 20px;
      width: 16px;
      transition: transform .3s ease;
    }

    &[data-active="true"] {
      border-bottom: 1px solid variables.$color-background-4;
      & ~ div.accordion-item__content {
        max-height: 500px;
        padding: 27px 16px 31px;
      }

      svg {
        transform: rotateZ(180deg);
      }
    }
  }
}
