@use "../abstracts/variables";
@use "../abstracts/mixins";

@mixin toggleDecorater($color) {
  @include mixins.linkEffect($color, 2px);
  bottom: -2px; // Overwrite
}

.carousel-products {
  h2 {
    margin-bottom: 8px;
    padding: 0 variables.$paddings-side--movil;
    text-transform: uppercase;
    text-align: center;
  }

  .swiper-slide {
    height: auto;
  }

  .swiper-button svg {
    height: 52px;
    width: 52px;
  }
}

.carousel-products__navigation {
  padding: 0 variables.$paddings-side--movil;
  max-width: 250px;
  margin: 0 auto 24px;
  @include mixins.boxFlex;

  @include mixins.media('tablet-l') {
    max-width: 400px;
  }
}

.carousel-products__navigation-btn {
  color: variables.$color-title;
  font-size: variables.$font-size--general-up-s;
  line-height: 26px;
  text-transform: uppercase;
  opacity: .5;
  transition: all .3s;
  @include toggleDecorater(transparent);

  &:not(&:last-of-type) {
    margin-right: 58px;
  }

  &.active {
    font-weight: 600;
    opacity: 1;
    @include toggleDecorater(currentColor);
  }
}

.carousel-products__slides-container {
  overflow: hidden;
  @include mixins.boxFlex(initial, initial);
}

.carousel-products__slide {
  width: 100%;
  transition: all .4s;
  @include mixins.boxFlex(space-between, initial, column);

  @include mixins.media('tablet-l') {
    padding: 0 25px;
  }
}

.carousel-products__slide-btn {
  margin-top: 44px;
  @include mixins.boxFlex(center, initial);
}
