@use "../abstracts/variables";
@use "../abstracts/mixins";

@mixin toggleDecorater($color) {
  @include mixins.linkEffect($color, 2px);
  bottom: -2px;
}

@mixin structure {
  overflow-x: scroll;
  display: flex;

  &::-webkit-scrollbar {
    display: none;
  }

  & > * {
    min-width: 252px;
    margin-right: 16px;
    flex-shrink: 1;

    &:first-of-type {
      margin-left: 0;
    }

    &:last-of-type {
      margin-right: variables.$paddings-side--movil;
    }
  }
}

.preload-hidden{
  visibility: hidden;
}

.slider-section {
  &__title {
    text-align: center;
    margin-bottom: 10px;
  }

  & .slider_defer.structure-preload > .swiper-wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    padding: 0;
    width: 100%;
    @include structure;
  }

  .slider-container.wrapper-m {
    @include mixins.media('tablet-l') {
      max-width: 1680px;
    }
  }

  @media screen and (max-width: 768px) {
    .slider-container {
      padding: 0;
    }

    & .structure-preload.slider_defer > .swiper-wrapper {
      @include mixins.marginsInside();
      @include structure;
    }
  }

  position: relative;

  .wrapper__slider,
  .wrapper__slider .swiper-wrapper {
    position: initial;
  }

  .wrapper__slider button.swiper-button-prev {
    left: 33px;
  }

  .wrapper__slider button.swiper-button-next {
    right: 33px;
  }
}

.slider-wrapper {
  &[data-active="false"] {
    display: none;
  }
}

.slider-tabs {
  width: 100%;
  list-style: none;
  @include mixins.boxFlex();
  margin-bottom: 24px;

  &__item {
    color: variables.$color-title;
    font-size: variables.$font-size--general-up-s;
    font-weight: 500;
    line-height: 32px;
    text-transform: uppercase;
    opacity: 0.5;
    cursor: pointer;
    @include toggleDecorater(transparent);

    &:not(:first-child) {
      margin-left: 58px;
    }

    &[data-active="true"] {
      @include toggleDecorater(currentColor);
      opacity: 1;
      font-weight: 600;
    }

    &:hover {
      opacity: 0.75;
    }
  }
}

.slider-content {
  &.without-arrows {
    .swiper-wrapper {
      justify-content: flex-start;

      @include mixins.media("tablet") {
        justify-content: center;
      }
    }

    .swiper-button {
      display: none;
    }
  }

  .swiper-wrapper .swiper-slide {
    height: auto;
  }
}

.slider-button {
  display: block;
  width: fit-content;
  margin: 45px auto 0;
  font-size: variables.$font-size--general-m;
}
