.modal {
  display: none;
  left: 50%;
  overflow-y: auto;
  position: fixed;
  top: 50%;
  transform: translate(-50%,-50%);
  z-index: 4;
  max-height: 80%;
  width: 80%;

  @media (min-width: 1000px) {
    width: 916px;
    height: 710px;
  }

  &[data-active="true"] {
    display: block;
  }
}