@use "../abstracts/variables";
@use "../abstracts/mixins";

.search {
  .predictive__search {
    padding: 0;
    z-index: 0;
  }

  .blogs-wrapper--header {
    padding-left: variables.$paddings-side--movil;
    padding-right: variables.$paddings-side--movil;
  }

  .collection__products-term {
    margin-top: 7px;
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;
    color: variables.$color-principal;
    text-align: initial;

    @include mixins.media('phone-m') {
      margin: 7px auto 0;
      width: 435px;
    }
  }

  .collection__products-grid {
    position: relative;
  }
}

.search__empty {
  margin: 50px 0;
  @include mixins.boxFlex(center, center, column);

  @include mixins.media('tablet-l') {
    margin: 100px auto;
  }

  .collection-filters__remove {
    color: variables.$color-principal;
  }
}

.search__empty-title {
  font-weight: 400;
}

.search-icon {
  display: block;
  cursor: pointer;
  &[data-active="false"] {
    display: none;
  }
}

.header {
  #predictive-search-list,
  .predictive__search {
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.08);
  }
}

.predictive__search {
  left: 0;
  position: absolute;
  top: 0;
  z-index: 5;
  padding: 10px;
  width: 100%;
  background: variables.$color-white;
  @include mixins.boxFlex(center, center);

  &[data-active="false"] {
    display: none;
  }

  &[data-show="true"] {
    position: initial;
    z-index: 2;
  }

  .predictive__search-zoom-button {
    line-height: 0;
    right: 22px;
    position: absolute;

    svg {
      width: 20px;
      position: static;
    }
  }

  svg.icon-zoom {
    height: 15px;
    margin-left: 0;
    width: 15px;
    position: static;
  }

  .icon-zoom path {
    fill: variables.$color-black;
  }

  .predictive-search__heading,
  .wk-button,
  .input-container label {
    display: none;
  }

  &--form {
    width: 100%;
    @include mixins.boxFlex;

    @include mixins.mediaCustom('min-width: 1100px') {
      width: 435px;
    }

    & > form {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;

      .form__textfield {
        width: 100%;
      }
    }

    #Search {
      padding: 14px 20px 14px 16px ;
      border-radius: 0;
      font-size: variables.$font-size--general-m;
      background: variables.$color-white;
      color: variables.$color-black;

    }

    #Search::placeholder {
      font-size: variables.$font-size--general-up-s;
    }

    #Search{
      &[type=search]::-webkit-search-decoration,
      &[type=search]::-webkit-search-cancel-button,
      &[type=search]::-webkit-search-results-button,
      &[type=search]::-webkit-search-results-decoration {
       display: none;
      }
    }

    #predictive-search-list {
      position: absolute;
      background: variables.$color-white;
      left: 0;
      right: 0;
      top: 52px;
      z-index: 1;

      .loading-container {
        display: flex;
        justify-content: center;
      }

      #predictive-search-results, .loading-container {
        height: 100%;
        border-left: 1px solid variables.$color-secondary;
        border-right: 1px solid variables.$color-secondary;
        border-bottom: 1px solid variables.$color-secondary;
        padding: 20px 15px 20px 20px;
        max-height: 435px;
        overflow-y: auto;

        h3 {
          font-size: 14px;
          font-weight: 400;
          border-bottom: 1px solid variables.$color-secondary;
          padding: 8px 0 5px 0;
          margin: 0;
        }
      }

      ul {
        list-style: none;

        li {
          &:not(:first-of-type) {
            margin-top: 20px;
          }

          & > a {
            color: variables.$color-black;
          }

          &:hover {
            & > a {
              .subtitle--general {
                text-decoration: underline;
              }
            }
          }
        }
      }

      button[type="submit"] {
        margin: 0 auto;
        padding: 10px 42px;
      }
    }

    svg {
      width: 12px;
      margin-left: 5px;
      cursor: pointer;
    }
  }

  .cart-items__container {
    width: 100%;
    margin: 0 !important;
    grid-template-columns: 60px auto;

    img, &--image, &--product-title {
      width: 100%;
      height: 70px;
      object-fit: cover;
    }

    &--product-title {
      align-self: center;
      justify-self: center;
    }

    .cart-items__container--title {
      text-align: initial;
    }
  }

  .result__button {
    margin-top: 40px;
  }

  .predictive__empty {
    display: block;
    color: variables.$color-background-2;

    &:not(:first-of-type) {
      display: none;
    }
  }
}

.header .predictive__search {
  bottom: 0;
  left: 0;
  right: 0;
  top: 100%;
  height: 70px;
  padding: 0 variables.$paddings-side--movil;
  width: 100%;

  @include mixins.mediaCustom('min-width: 1100px') {
    left: 14%;
    top: 100%;
    height: 56px;
    padding: 0;
    width: 350px;
  }

  @include mixins.mediaCustom('min-width: 1350px') {
    left: 200px;
    width: 421px;
  }
  .predictive__search--form {
    @include mixins.mediaCustom('min-width: 1100px') {
    }
  }

  #Search {
    @include mixins.mediaCustom('min-width: 1100px') {
      border: none;
    }
  }

  #predictive-search-results {
    @include mixins.mediaCustom('min-width: 1100px') {
      border-top: 1px solid variables.$color-background;
    }
  }
}

#overlay--search-predictive {
  @include mixins.mediaCustom('min-width: 1100px') {
    background-color: transparent;
  }
}

.cart-upsell-container-product__info-vendor.subtitle--general,
.cart-items__container--vendor.subtitle--general span {
  font-family: variables.$font-family--title;
  font-size: variables.$font-size--general-m;
  line-height: variables.$line-height--general-m;
  font-weight: 900;
  color: variables.$color-black;
  text-transform: uppercase;
}

// Predictive search SUPPORT

li.predictive-search__list-item.not_available {
  opacity: 0.5;
}

#predictive_item_sold {
  margin-left: variables.$margin-s;
  background-color: lightgray;
  padding: 0 8px;
}