.result__search {
  &--header {
    margin-top: 45px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &--list {
    margin: 20px 0;
    &-head {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 10px;
    }
  }

  &--empty {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
  }
}