@use "../abstracts/mixins";

.letter-icon {
  position: absolute;
  display: none;

  @include mixins.media('tablet') {
   display: block;
  }

  &--top-right {
    top: 12px;
    right: 0;

    @include mixins.media('tablet') {
      top: 100px;
      right: 0;
    }
  }

  &--top-left {
    @include mixins.media('tablet') {
      top: -150px;
      left: 0px;
    }
  }

  &--bottom-right {
    bottom: -25px;
    right: 0;

    @include mixins.media('tablet') {
      bottom: -15px;
    }
  }

  &--bottom-left {
    left: -25px;
    bottom: 0;

    @include mixins.media('tablet') {
      left: 0;
    }
  }
}
